<template>
    <div class="container mt-5">
        <div class="card" v-if="!graphCreated">
            <div class="card-header">
                <h2>Formulaire de Réseau de Télécommunications</h2>
            </div>
            <div class="card-body">
                <form @submit.prevent="handleGraphCreation">
                    <div class="mb-3">
                        <label for="nodes" class="form-label">Nœuds:</label>
                        <input type="text" id="nodes" v-model="nodes" class="form-control"
                            placeholder="ex: A, B, C, D, E, F, G, H" />
                    </div>
                    <div class="mb-3">
                        <label for="edges" class="form-label">Liaisons et poids:</label>
                        <input type="text" id="edges" v-model="edges" class="form-control"
                            placeholder="ex: A-B-1, A-C-3, B-C-1, B-D-4, etc." />
                    </div>
                    <button type="submit" class="btn btn-primary">Créer le graphique</button>
                </form>
            </div>
        </div>

        <div v-if="graphCreated">
            <div class="card mb-4">
                <div class="card-header">
                    <h2>Réseau de télécommunications</h2>
                </div>
                <div class="card-body text-center">
                    <img :src="'data:image/png;base64,' + graphImg" alt="Graph" class="img-fluid" />
                    <div class="mt-3">
                        <button @click="calculateMST" class="btn btn-primary mb-3">Arbre couvrant minimal
                            (Kruskal)</button>
                        <button @click="showShortestPathForm" class="btn btn-success mb-3">Chemin le plus
                            court(dijkstra)</button>
                    </div>

                    <div class="mt-3 nodes-edges-info">
                        <h3>Nœuds et Arêtes</h3>
                        <p><strong>Nœuds:</strong> {{ nodes }}</p>
                        <p><strong>Arêtes:</strong> {{ edges }}</p>
                    </div>
                    <button @click="resetForm" class="btn btn-secondary mt-3">Retour à l'accueil</button>
                </div>
            </div>
        </div>

        <div v-if="showPathForm">
            <div class="card mb-4">
                <div class="card-header">
                    <h2>Chemin le plus court</h2>
                </div>
                <div class="card-body">
                    <form @submit.prevent="calculateShortestPath">
                        <div class="mb-3">
                            <label for="start" class="form-label">Nœud de départ:</label>
                            <input type="text" id="start" v-model="start" class="form-control" />
                        </div>
                        <div class="mb-3">
                            <label for="end" class="form-label">Nœud de fin:</label>
                            <input type="text" id="end" v-model="end" class="form-control" />
                        </div>
                        <button type="submit" class="btn btn-primary">Calculer</button>
                    </form>
                </div>
            </div>
        </div>

        <div v-if="mstImg">
            <div class="card mb-4">
                <div class="card-header">
                    <h2>Arbre couvrant minimal (Kruskal)</h2>
                </div>
                <div class="card-body text-center">
                    <img :src="'data:image/png;base64,' + mstImg" alt="MST" class="img-fluid" />
                </div>
            </div>
        </div>

        <div v-if="pathImg">
            <div class="card mb-4">
                <div class="card-header">
                    <h2>Chemin le plus court</h2>
                </div>
                <div class="card-body text-center">
                    <img :src="'data:image/png;base64,' + pathImg" alt="Path" class="img-fluid" />
                    <p class="mt-3"><strong>Chemin entre {{ start }} et {{ end }}:</strong> {{ path }}</p>
                    <p><strong>Longueur totale du chemin:</strong> {{ length }}</p>
                </div>
            </div>
        </div>

        <div v-if="loading" class="loading-overlay">
            <button class="btn btn-primary" type="button" disabled>
                <span class="spinner-grow spinner-grow-sm" aria-hidden="true"></span>
                <span role="status">Chargement du graph...</span>
            </button>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'HelloWorld',
    data() {
        return {
            nodes: '',
            edges: '',
            start: '',
            end: '',
            graphImg: null,
            mstImg: null,
            pathImg: null,
            path: null,
            length: null,
            graphCreated: false,
            showPathForm: false,
            loading: false,
            pos: '',
        };
    },
    methods: {
        parseNodes(nodesString) {
            return nodesString.split(',').map(node => ({ id: node.trim() }));
        },
        parseEdges(edgesString) {
            return edgesString.split(',').map(edge => {
                const [node1, node2, weight] = edge.split('-');
                return { node1: node1.trim(), node2: node2.trim(), weight: parseInt(weight.trim()) };
            });
        },
        validateInput() {
            if (!this.nodes || !this.edges) {
                alert('Veuillez entrer les nœuds et les arêtes.');
                return false;
            }
            const nodesArray = this.nodes.split(',').map(node => node.trim());
            const edgesArray = this.edges.split(',').map(edge => edge.split('-'));
            for (let edge of edgesArray) {
                if (edge.length !== 3 || !nodesArray.includes(edge[0].trim()) || !nodesArray.includes(edge[1].trim()) || isNaN(edge[2].trim())) {
                    alert('Veuillez entrer les arêtes au format correct (ex: A-B-1, B-C-2).');
                    return false;
                }
            }
            return true;
        },
        handleGraphCreation() {
            if (!this.validateInput()) return;

            const nodes = this.parseNodes(this.nodes);
            const edges = this.parseEdges(this.edges);

            this.loading = true;

            axios.post('https://reseaux-telecom.onrender.com/telecom/graph', {
                nodes: nodes,
                edges: edges,
            })
                .then(response => {
                    this.graphImg = response.data.plot_url;
                    this.graphCreated = true;
                    this.pos = response.data.pos
                })
                .catch(error => {
                    console.error('Error creating graph:', error);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        calculateMST() {
            const nodes = this.parseNodes(this.nodes);
            const edges = this.parseEdges(this.edges);

            this.loading = true;

            axios.post('https://reseaux-telecom.onrender.com/telecom/mst', {
                nodes: nodes,
                edges: edges
            })
                .then(response => {
                    this.mstImg = response.data.plot_url;
                })
                .catch(error => {
                    console.error(error);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        showShortestPathForm() {
            this.showPathForm = true;
        },
        calculateShortestPath() {
            if (!this.validateInput()) return;

            const nodes = this.parseNodes(this.nodes);
            const edges = this.parseEdges(this.edges);


            this.loading = true;

            axios.post('https://reseaux-telecom.onrender.com/telecom/shortest-path/', {
                graph: {
                    nodes: nodes,
                    edges: edges,
                    pos: this.pos
                },
                start: this.start,
                end: this.end,


            },)
                .then(response => {
                    this.pathImg = response.data.plot_url;
                    this.path = response.data.path;
                    this.length = response.data.length;
                })
                .catch(error => {
                    console.error(error);
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        resetForm() {
            this.nodes = '';
            this.edges = '';
            this.start = '';
            this.end = '';
            this.graphImg = null;
            this.mstImg = null;
            this.pathImg = null;
            this.path = null;
            this.length = null;
            this.graphCreated = false;
            this.showPathForm = false;
        }
    }
};
</script>

<style scoped>
.logo-container {
    text-align: center;
    margin-bottom: 20px;
}

.logo {
    height: 6em;
}

.card-header {
    background-color: #007bff;
    color: white;
}

.card {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.img-fluid {
    max-width: 100%;
    max-height: 500px;
    /* Nouvelle hauteur maximale */
    height: auto;
}

.btn {
    margin-right: 10px;
}

.nodes-edges-info {
    text-align: left;
    margin-top: 20px;
    padding: 10px;
    background-color: #f8f9fa;
    border-radius: 5px;
}

.loading-overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255, 255, 255, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
}

.spinner-border {
    width: 3rem;
    height: 3rem;
    color: #007bff;
}
</style>
